"use client";

import { useState } from "react";
import {
  Card,
  CardContent,
  Typography,
  TextField,
  Button,
  Box,
  Alert,
  Snackbar,
  FormControl,
  InputLabel,
  OutlinedInput,
  InputAdornment,
  CircularProgress,
} from "@mui/material";
import { styled } from "@mui/material/styles";

interface AddProductFormProps {
  onProductAdded: () => void;
}

interface ProductFormData {
  name: string;
  price: string;
  description: string;
  image: string;
  stock: string;
}

const StyledCard = styled(Card)(({ theme }) => ({
  margin: theme.spacing(2),
  boxShadow: theme.shadows[4],
}));

const StyledButton = styled(Button)(() => ({
  minHeight: "48px",
  fontWeight: "bold",
}));

export default function AddProductForm({
  onProductAdded,
}: AddProductFormProps) {
  const [formData, setFormData] = useState<ProductFormData>({
    name: "",
    price: "",
    description: "",
    image: "",
    stock: "",
  });
  const [isSubmitting, setIsSubmitting] = useState(false);
  const [error, setError] = useState<string | null>(null);
  const [success, setSuccess] = useState(false);

  const handleInputChange = (
    e: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement>
  ) => {
    const { name, value } = e.target;
    setFormData((prev) => ({
      ...prev,
      [name]: value,
    }));
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    setIsSubmitting(true);
    setError(null);
    setSuccess(false);

    try {
      // È©óË≠âÂøÖÂ°´Ê¨Ñ‰Ωç
      if (!formData.name.trim()) {
        throw new Error("ÂïÜÂìÅÂêçÁ®±ÁÇ∫ÂøÖÂ°´È†ÖÁõÆ");
      }
      if (!formData.price.trim()) {
        throw new Error("ÂïÜÂìÅÂÉπÊ†ºÁÇ∫ÂøÖÂ°´È†ÖÁõÆ");
      }

      const price = parseFloat(formData.price);
      if (isNaN(price) || price <= 0) {
        throw new Error("Ë´ãËº∏ÂÖ•ÊúâÊïàÁöÑÂÉπÊ†º");
      }

      const stock = formData.stock ? parseInt(formData.stock) : undefined;
      if (stock !== undefined && (isNaN(stock) || stock < 0)) {
        throw new Error("Ë´ãËº∏ÂÖ•ÊúâÊïàÁöÑÂ∫´Â≠òÊï∏Èáè");
      }

      const productData = {
        name: formData.name.trim(),
        price: price,
        description: formData.description.trim() || undefined,
        image: formData.image.trim() || undefined,
        stock: stock,
      };

      const response = await fetch("/api/products", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify(productData),
      });

      if (!response.ok) {
        const errorData = await response.json().catch(() => ({}));
        throw new Error(
          errorData.message || `HTTP error! status: ${response.status}`
        );
      }

      const newProduct = await response.json();
      console.log("Êñ∞Â¢ûÂïÜÂìÅÊàêÂäü:", newProduct);

      setSuccess(true);
      setFormData({
        name: "",
        price: "",
        description: "",
        image: "",
        stock: "",
      });

      // ÈÄöÁü•Áà∂ÁµÑ‰ª∂Êõ¥Êñ∞ÂïÜÂìÅÂàóË°®
      onProductAdded();

      // 3ÁßíÂæåÈö±ËóèÊàêÂäüË®äÊÅØ
      setTimeout(() => setSuccess(false), 3000);
    } catch (err) {
      setError(err instanceof Error ? err.message : "Êñ∞Â¢ûÂïÜÂìÅÂ§±Êïó");
      console.error("Êñ∞Â¢ûÂïÜÂìÅÊôÇÁôºÁîüÈåØË™§:", err);
    } finally {
      setIsSubmitting(false);
    }
  };

  const handleReset = () => {
    setFormData({
      name: "",
      price: "",
      description: "",
      image: "",
      stock: "",
    });
    setError(null);
    setSuccess(false);
  };

  return (
    <>
      <StyledCard>
        <CardContent>
          <Box display="flex" alignItems="center" mb={3}>
            <Typography variant="h4" component="h2" fontWeight="bold">
              ‚ûï Êñ∞Â¢ûÂïÜÂìÅ
            </Typography>
          </Box>

          {error && (
            <Alert severity="error" sx={{ mb: 3 }}>
              <Typography variant="h6">Êñ∞Â¢ûÂ§±Êïó</Typography>
              <Typography>{error}</Typography>
            </Alert>
          )}

          <Box component="form" onSubmit={handleSubmit}>
            <Box sx={{ display: "flex", flexDirection: "column", gap: 3 }}>
              <Box sx={{ display: "flex", gap: 2, flexWrap: "wrap" }}>
                <TextField
                  fullWidth
                  label="ÂïÜÂìÅÂêçÁ®±"
                  name="name"
                  value={formData.name}
                  onChange={handleInputChange}
                  required
                  placeholder="Ë´ãËº∏ÂÖ•ÂïÜÂìÅÂêçÁ®±"
                  variant="outlined"
                  helperText="ÂøÖÂ°´È†ÖÁõÆ"
                  sx={{ flex: "1 1 300px" }}
                />

                <FormControl sx={{ flex: "1 1 300px" }} variant="outlined">
                  <InputLabel htmlFor="price">ÂÉπÊ†º</InputLabel>
                  <OutlinedInput
                    id="price"
                    name="price"
                    type="number"
                    value={formData.price}
                    onChange={handleInputChange}
                    required
                    inputProps={{ min: "0", step: "0.01" }}
                    startAdornment={
                      <InputAdornment position="start">NT$</InputAdornment>
                    }
                    label="ÂÉπÊ†º"
                    placeholder="Ë´ãËº∏ÂÖ•ÂïÜÂìÅÂÉπÊ†º"
                  />
                </FormControl>
              </Box>

              <TextField
                fullWidth
                label="ÂïÜÂìÅÊèèËø∞"
                name="description"
                value={formData.description}
                onChange={handleInputChange}
                multiline
                rows={3}
                placeholder="Ë´ãËº∏ÂÖ•ÂïÜÂìÅÊèèËø∞ÔºàÈÅ∏Â°´Ôºâ"
                variant="outlined"
              />

              <Box sx={{ display: "flex", gap: 2, flexWrap: "wrap" }}>
                <TextField
                  fullWidth
                  label="ÂïÜÂìÅÂúñÁâáÁ∂≤ÂùÄ"
                  name="image"
                  type="url"
                  value={formData.image}
                  onChange={handleInputChange}
                  placeholder="Ë´ãËº∏ÂÖ•ÂúñÁâáÁ∂≤ÂùÄÔºàÈÅ∏Â°´Ôºâ"
                  variant="outlined"
                  sx={{ flex: "1 1 300px" }}
                />

                <TextField
                  fullWidth
                  label="Â∫´Â≠òÊï∏Èáè"
                  name="stock"
                  type="number"
                  value={formData.stock}
                  onChange={handleInputChange}
                  inputProps={{ min: "0" }}
                  placeholder="Ë´ãËº∏ÂÖ•Â∫´Â≠òÊï∏ÈáèÔºàÈÅ∏Â°´Ôºâ"
                  variant="outlined"
                  sx={{ flex: "1 1 300px" }}
                />
              </Box>
            </Box>

            <Box display="flex" gap={2} mt={4}>
              <StyledButton
                type="submit"
                variant="contained"
                size="large"
                disabled={isSubmitting}
                startIcon={
                  isSubmitting ? (
                    <CircularProgress size={20} color="inherit" />
                  ) : (
                    <span>‚ûï</span>
                  )
                }
                sx={{ flex: 1 }}
              >
                {isSubmitting ? "Êñ∞Â¢û‰∏≠..." : "Êñ∞Â¢ûÂïÜÂìÅ"}
              </StyledButton>

              <StyledButton
                type="button"
                variant="outlined"
                size="large"
                onClick={handleReset}
                startIcon={<span>üîÑ</span>}
              >
                ÈáçË®≠
              </StyledButton>
            </Box>
          </Box>
        </CardContent>
      </StyledCard>

      <Snackbar
        open={success}
        autoHideDuration={3000}
        onClose={() => setSuccess(false)}
        anchorOrigin={{ vertical: "top", horizontal: "center" }}
      >
        <Alert severity="success" onClose={() => setSuccess(false)}>
          <Typography variant="h6">Êñ∞Â¢ûÊàêÂäüÔºÅ</Typography>
          <Typography>ÂïÜÂìÅÂ∑≤ÊàêÂäüÊñ∞Â¢ûÂà∞ÂàóË°®‰∏≠</Typography>
        </Alert>
      </Snackbar>
    </>
  );
}
